import React from 'react'
import Home_image from "../Assets/Home.svg";


function Home() {
    return (
      <div className="home">
        <div className="home_inner home_text">
          <h1 className="home_text_1">6th National Level </h1>
          <h1 className="home_text_2">ACM WOMEN'S</h1>
          <h1 className="home_text_3">Hackathon</h1>
          <h1 className="home_text_4">2021</h1>
          <button className="home_register">
            <a href="/register">REGISTER NOW</a>
          </button>
        </div>
        <div className="home_inner">
          <img src={Home_image} alt="home" className="home_image" />
        </div>
      </div>
    );
}

export default Home
