import React, { useState, useEffect } from "react";
import Reg_img from "../Assets/Reg_img.svg";
import Button from "@material-ui/core/Button";
import Box from "@material-ui/core/Box";
import fire from "../config";
import { useHistory } from "react-router-dom";
const getUuid = require("uuid-by-string");
function Register() {
  let history = useHistory();
  var otp = () => {
    return Math.random().toString().substring(2, 8);
  };

  var [form, setForm] = useState({
    otp: otp(),
    TeamName: "",
    TeamMember: "TeamLeader",
    TeamLeader: {
      fname: "",
      lname: "",
      mail: "",
      phone: "",
      degree: "",
      branch: "",
      city: "",
      univ: "",
    },
    Mem1: {
      fname: "",
      lname: "",
      mail: "",
      phone: "",
      degree: "",
      branch: "",
      city: "",
      univ: "",
    },
    Mem2: {
      fname: "",
      lname: "",
      mail: "",
      phone: "",
      degree: "",
      branch: "",
      city: "",
      univ: "",
    },
    Men: {
      fname: "",
      lname: "",
      mail: "",
      phone: "",
      degree: "",
      branch: "",
      city: "",
      univ: "",
    },
  });

  useEffect(() => {}, [form.TeamMember]);

  var handleChange = (e) => {
    const value = e.target.value;
    const key = e.target.id;
    setForm({ ...form, [key]: value });
  };

  var handleSelect = (e) => {
    const value = e.target.value;
    const key = e.target.name;
    setForm({ ...form, [key]: value });
  };

  var handleForm = (e) => {
    var key = e.target.id; // TeamMember
    var name = e.target.name; // fname
    var value = e.target.value; // value
    setForm((prev) => ({
      ...prev,
      [key]: { ...prev[key], [name]: value },
    }));
  };

  var check = () => {
    if (
      form.TeamLeader.mail === "" ||
      form.Mem1.mail === "" ||
      form.Mem2.mail === "" ||
      form.Men.mail === ""
    ) {
      return false;
    } else {
      return true;
    }
  };

  var handleSubmit = (e) => {
    e.preventDefault();
    if (check()) {
      fire
        .database()
        .ref("form")
        .push(form)
        .then(() => {
          console.log("done");
          const Pform = { ...form, otp: getUuid(form.otp) };
          fire
            .database()
            .ref("Pform")
            .push(Pform)
            .then(() => {
              history.push("/");
            });
        });
    } else {
      alert("Please fill all form values for all the Participants");
    }
  };

  return (
    <div className="register">
      <div className="reg_img_wrapper">
        <img src={Reg_img} alt="register page" className="reg_img" />
      </div>
      <div className="reg_form">
        <h2 className="reg_heading">REGISTER NOW</h2>
        <form onSubmit={handleSubmit}>
          <label for="TeamName">Team Name</label>
          <input
            type="text"
            required
            value={form.TeamName}
            id="TeamName"
            onChange={handleChange}
          />
          <label for="TeamMember">Team Member</label>
          <select
            id="TeamMember"
            value={form.TeamMember}
            onChange={handleSelect}
            name="TeamMember"
          >
            <option value={"TeamLeader"}>Team Leader</option>
            <option value={"Mem1"}>Team Member 1</option>
            <option value={"Mem2"}>Team Member 2</option>
            <option value={"Men"}>Team Mentor</option>
          </select>
          <div className="f_cont">
            <div>
              <label for={form.TeamMember}>First Name</label>
              <input
                required
                value={form[form.TeamMember].fname}
                id={form.TeamMember}
                name="fname"
                onChange={handleForm}
              />
            </div>
            <div>
              <label for={form.TeamMember}>Last Name</label>
              <input
                required
                value={form[form.TeamMember].lname}
                id={form.TeamMember}
                name="lname"
                onChange={handleForm}
              />
            </div>
          </div>

          <div className="f_cont">
            <div>
              <label for={form.TeamMember}>E-Mail ID</label>
              <input
                required
                type="email"
                value={form[form.TeamMember].mail}
                id={form.TeamMember}
                name="mail"
                onChange={handleForm}
              />
            </div>

            <div>
              {" "}
              <label for={form.TeamMember}>Phone Number</label>
              <input
                required
                type="tel"
                value={form[form.TeamMember].phone}
                id={form.TeamMember}
                name="phone"
                onChange={handleForm}
              />
            </div>
          </div>
          <div className="f_cont">
            <div>
              <label for={form.TeamMember}>University</label>
              <input
                required
                value={form[form.TeamMember].univ}
                id={form.TeamMember}
                name="univ"
                onChange={handleForm}
              />
            </div>
            <div>
              <label for={form.TeamMember}>City</label>
              <input
                required
                value={form[form.TeamMember].city}
                id={form.TeamMember}
                name="city"
                onChange={handleForm}
              />
            </div>
          </div>
          <div className="f_cont">
            <div>
              <label for={form.TeamMember}>Degree</label>
              <input
                required
                value={form[form.TeamMember].degree}
                id={form.TeamMember}
                name="degree"
                onChange={handleForm}
              />
            </div>
            <div>
              <label for={form.TeamMember}>Branch</label>
              <input
                required
                value={form[form.TeamMember].branch}
                id={form.TeamMember}
                name="branch"
                onChange={handleForm}
              />
            </div>
          </div>

          <Box textAlign="center" p={1}>
            <Button variant="contained" color="primary" type="submit">
              Submit
            </Button>
          </Box>
        </form>
      </div>
    </div>
  );
}

export default Register;
