import React from "react";
import "../Theme.scss";
import Edu from "../Assets/Edu.gif";
import Sec from "../Assets/Security.gif";
import Heal from "../Assets/Health.gif";
import Fin from "../Assets/Fintech.gif";
import ai from "../Assets/aigif.gif";
import Modal from "@material-ui/core/Modal";

export default function Theme() {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  return (
    <>
      <div className="theme_container">
        <h1>THEME</h1>
        <h2>Innovating with Technologies</h2>
        <div className="ou">
          <img src={Edu}></img>
          <div className="in">
            <h3>Education</h3>
            <p>
              It thrives to be the driving force to new innovations and growth
              of an individual. Innovating and implementation of ideas to
              improve education and make it easily available to the public would
              be a solution to look out for.
            </p>
            {/* <button onClick={handleOpen}>Problem Statements</button> */}
          </div>
        </div>
        <div className="ou">
          <div className="in">
            <h3>Security</h3>
            <p>
              It is a process and not a product, data breaches have accounted
              for numerous issues in the system. It is the tool to achieving
              complete privacy.
            </p>
            {/* <button onClick={handleOpen}>Problem Statements</button> */}
          </div>
          <img src={Sec}></img>
        </div>
        <div className="ou">
          <img src={Heal}></img>
          <div className="in">
            <h3>Healthcare</h3>
            <p>
              Health is the real wealth and technology is ushering the health
              care which today, in this pandemic, has held a key value to the
              society.
            </p>
            {/* <button onClick={handleOpen}>Problem Statements</button> */}
          </div>
        </div>
        <div className="ou">
          <div className="in">
            <h3>Fintech</h3>
            <p>
              The growing age of cryptocurrency and online transactions have
              become the modern age. But it still has a few stones unturned.
            </p>
            {/* <button onClick={handleOpen}>Problem Statements</button> */}
          </div>
          <img src={Fin}></img>
        </div>
        <div className="ou">
          <img src={ai}></img>
          <div className="in">
            <h3>Smart Systems</h3>
            <p>
              Automation and making processes simpler and easier to make the
              world efficient is what a smart system can do
            </p>
            {/* <button onClick={handleOpen}>Problem Statements</button> */}
          </div>
        </div>
      </div>
      <Modal open={open} onClose={handleClose}>
        <div className="modal">
          <h2>Problem Statements</h2>
          
        </div>
      </Modal>
    </>
  );
}
