import React from "react";
import logo from "../Assets/logo.png";
import "../menu.scss";
import Grow from "@material-ui/core/Grow";
function Navbar({ page }) {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  return (
    <>
      <div className="navbar">
        <img src={logo} alt="logo" className="acm_logo" />
        <div className="nav_text_wrapper">
          <a href="/">
            {" "}
            <h2>MUJ ACM</h2>{" "}
          </a>
          {!page && <h4>PRESENTS</h4>}
        </div>

        {open ? (
          <i onClick={handleClose} class="fas fa-times menu"></i>
        ) : (
          <i onClick={handleOpen} class="fas fa-bars menu"></i>
        )}
        {/* <i onClick={handleOpen} className="fas fa-bars menu"></i> */}

        {open && (
          <Grow in={open}>
            <div className="ham-menu">
              <div className="in-men">
                <a href="/#Home" onClick={handleClose}>
                  Home
                </a>
              </div>{" "}
              <div className="in-men">
                <a href="/Register" onClick={handleClose}>
                  Register
                </a>
              </div>
              <div className="in-men">
                <a href="/#About" onClick={handleClose}>
                  About Us
                </a>
              </div>{" "}
              <div className="in-men">
                <a href="/#Contests" onClick={handleClose}>
                  Contest
                </a>
              </div>{" "}
              <div className="in-men">
                <a href="/#Theme" onClick={handleClose}>
                  Theme
                </a>
              </div>
              <div className="in-men">
                <a href="/#Rules" onClick={handleClose}>
                  Rules
                </a>
              </div>
            </div>
          </Grow>
        )}
      </div>
    </>
  );
}

export default Navbar;
