import React from "react";
import "../rules.scss";
function Rules() {
  return (
    <div className="rules">
      <h1>RULES AND REGULATIONS</h1>
      <div className="inner_rules">
        <div>
          <h1>Round 1</h1>
          <ul>
            <li>Each team can have maximum 3 female members.</li>
            <li>
              Any number of teams from a given Institution can participate in
              the event.
            </li>
            <li>
              Participants have to register for the Hackathon through the site.
              Refer to the Registration page for more details.
            </li>
            <li>20 selected teams will move forward for the second round.</li>
          </ul>
        </div>
        <div>
          <h1>Round 2</h1>
          <ul>
            <li>
              All three members of each selected team should attend the Second
              Round.
            </li>
            <li>All team members should carry their College ID.</li>
            <li>Turning your camera on is necessary during this round.</li>
            <li>
              All the details of the round will be sent to selected teams
              through email.
            </li>
            <li>
              All the members should be ready at least 15 minutes prior to the
              allotted time slot.
            </li>
            <li>Top 10 selected teams will enter into finale round.</li>
          </ul>
        </div>
        <div>
          <h1>Round 3</h1>
          <ul>
            <li>
              If a participant from a Team cannot join the Team for the Finals,
              the college can replace the student with another participant with
              consent from the organizers.
            </li>
            <li>
              Turning your camera on is necessary during Evaluation round.
            </li>
            <li>
              The Team Lead will create a Github repository with the name
              "ACM-W2021_TeamName" and add all the team members as collaborators
              along with two invigilating officers whose Github ID's will be
              shared to selected teams.
            </li>
            <li>
              The invigilating officers will be continuously checking your
              commits to ensure that no unfair means are being used.
            </li>
            <li>
              All the teams should join the mentoring sessions as well as
              evaluation rounds at the allotted time only.
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Rules;
